<template>
  <div class="updateGood">
    <el-card class="updateGood-card">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <el-card style="margin-top: 20px;" class="box-card">
        <div slot="header" class="clearfix">
          <span></span>
          <el-button style="float: right; padding: 3px 0" type="text"></el-button>
        </div>
        <el-form
          label-position="left"
          label-width="100px"
          style="height: 600px;overflow-y: scroll;"
          ref="updateFormRef"
          :model="willUpdateGood" >
          <el-form-item label="">
            <el-input v-model="willUpdateGood.goodsName"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="number" v-model="willUpdateGood.goodsNumber"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="willUpdateGood.goodsPrice"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="willUpdateGood.goodsWeight"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="willUpdateGood.hotNumber"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch style="margin-left: 20px" v-model="willUpdateGood.isPromote" active-text="" inactive-text=""/>
          </el-form-item>
          <el-form-item label="">
            <el-radio v-model="willUpdateGood.goodsState" label="0" border size="medium"></el-radio>
            <el-radio v-model="willUpdateGood.goodsState" label="1" border size="medium"></el-radio>
            <el-radio v-model="willUpdateGood.goodsState" label="2" border size="medium"></el-radio>
          </el-form-item>
          <el-form-item label="">
            <div v-for="(item,index) in dynamicData">
              <el-tag>{{item.attrName}}</el-tag>
              <el-checkbox-group
                v-model="willUpdateGood.attrs[1][index].dynamicValue"
                :disabled="false"
                style="margin: 10px 0">
                <el-checkbox
                  v-for="(child,index) in item.attrValues"
                  :key="index"
                  :label="child"
                  style="margin: 10px 10px"
                  border/>
              </el-checkbox-group>
            </div>
          </el-form-item>
          <el-form-item label="">
            <el-upload
              class="upload-demo"
              :headers="headers"
              name = "image"
              accept="image/*"
              action="/api/goodimgupload"
              :on-remove="handleRemove"
              :before-upload = 'beforeUpload'
              :on-success="onSuccessHandle"
              :before-remove="beforeRemove"
              :file-list="fileList"
              list-type="picture">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <quill-editor
              v-model="willUpdateGood.goodsIntroduce"
              ref="Editor"
              :options="editorOption"/>
          </el-form-item>
        </el-form>
      </el-card>
    </el-card>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  /*import 'quill/dist/quill.snow.css'*/
  import { quillEditor } from 'vue-quill-editor';
  import {
    requestCategoryList,
    requestImageDelete,
    requestUpdateGoodImage } from '../../../../../ajax/index'
  export default {
    data() { // 
      return {
        props:{
          fileList:[],
          value: '_id',
          label: 'categoryName',
        },
        editorOption: {
          placeholder: '',
          theme: 'snow',
        },
        headers:{}, // Authorization
        dynamicData:[], // 
        staticData:[], // 
      }
    },
    computed: { // 
      ...mapState(['willUpdateGood'])
    },
    created () { // 
      if(!this.willUpdateGood.goodsName){
        this.$router.replace('/productslist')
      }else{
        this.headers.Authorization = window.sessionStorage.getItem('token')
        this.fileList = this.willUpdateGood.goodsImages
        this.getRequestCategoryList()
      }
    },
    methods: { // 
      // 
      getRequestUpdateGoodImage(fileList,_id){
        let goodsImages = []
        let imgObj
        fileList.map((item) =>{
          imgObj = {}
          imgObj.name = item.name
          imgObj.url = item.url
          goodsImages.push(imgObj)
        })
        return new Promise(async (resolve,reject) =>{
          const result = await requestUpdateGoodImage({goodsImages,_id})
          console.log(result)
          if(result.code === 0){
            resolve(result.data)
          }else{
            reject(result.data)
          }
        })
      },
      // 
      getRequestImageDelete({name}){
        return new Promise(async (resolve, reject) =>{
          const result = await requestImageDelete({name})
          console.log(result)
          if(result.code === 0){
            resolve(true)
          }else{
            reject(false)
          }
        })
      },
      // 
      beforeUpload(file){
        let fileName = file.name.substring(file.name.lastIndexOf('.')+1)
        let testArr = ['jpeg','png','jpg','gif']
        if(!testArr.includes(fileName)){
          this.$message.error('\'jpeg\',\'png\',\'jpg\',\'gif\'')
          return false
        }
      },
      // 
      onSuccessHandle(response, file, fileList){
        const {code,data} = response
        console.log(response)
        if(code === 0){
          file.name = data.name
          file.url = data.url
          this.getRequestUpdateGoodImage(fileList,this.willUpdateGood._id)
            .then(() =>{
                this.$message.success('')
              },
              () =>{
                this.$message.error('')
                fileList.splice(fileList.length-1,1)
              })
        }else {
          if(code === 1){ this.$message.error('')}
          else if(code === 2){this.$message.error('')}
          else{this.$message.error('')}
          fileList.splice(fileList.length-1,1)
        }
        this.willUpdateGood.goodsImages = fileList
        this.fileList = fileList
        console.log(fileList)
      },
      // 
      beforeRemove(file,fileList){
        return new Promise(async (resolve, reject) =>{
          const result = await this.$confirm(`${file.name}, ?`, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).catch(e =>{})
          if(result){ // 
            resolve(true)
          }else{
            reject(false)
          }
        })
      },
      // 
      handleRemove(file,fileList){
        this.getRequestUpdateGoodImage(fileList,this.willUpdateGood._id)
          .then(() =>this.getRequestImageDelete({name:file.name}))
          .then(()=> this.$message.success(''))
          .catch(e => {
            this.$message.error('')
            fileList.push(file)
          })
        this.fileList = fileList
        this.willUpdateGood.goodsImages = fileList
      },
      async getRequestCategoryList(){
        const result = await requestCategoryList({categoryId:this.willUpdateGood.categoryId,attrSel:'all'})
        if(result.code === 0){
          const {dynamicData,staticData} = result.data
          this.dynamicData = dynamicData
          this.staticData = staticData
        }
      }
    },
    components: { // 
      quillEditor
    }
  }
  /*
    beforeCreate () { // 
      
    },
    created () { // 
      
    },
    beforeMount () { // 
      
    },
    mounted() { //
      
    },
    beforeUpdate() { // 
      
    },
    updated() { // 
      
    },
    beforeDestroy() { // 
      
    },
    destroyed() { // 
      
    },
    computed: { // 
      
    },
    watch: { // 
      
    },
    */
</script>
<style scoped lang='less'>
.updateGood{
  height: 100%;
  width: 100%;
  .updateGood-card{
    height: 100%;
    width: 100%;
    .changeCategory{
      position: relative;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
    }
  }
}
</style>