<template>
  <div>
    <el-upload
      class="upload-demo"
      action="/api/goodimgupload"
      :headers="headers"
      name = "image"
      accept="image/*"
      :on-preview="onPreview"
      :before-upload = 'beforeUpload'
      :on-success="onSuccessHandle"
      :file-list="addForm.fileList"
      :before-remove="beforeRemove"
      :on-remove="handleRemove"
      list-type="picture">
      <el-button size="small" type="primary">Giao</el-button>
      <div slot="tip" class="el-upload__tip">jpeg/png/jpg/gif</div>
    </el-upload>
    <BigImage :url="url" v-on:openBigImageDialog="openBigImageDialog"/>
  </div>
</template>
<script>
  import {requestImageDelete} from '../../../../../../../ajax/index'
  import BigImage from './bigImage'
  import {mapState} from 'vuex'
  export default {
    data() { // 
      return {
        url:'',
        headers:{} // Authorization
      }
    },
    created () { // 
      const Authorization = window.sessionStorage.getItem('token')
      this.headers.Authorization = Authorization
    },
    computed: { // 
      ...mapState(['addForm'])
    },
    methods: { // 
      openBigImageDialog(fun){
        this.fun = fun
      },
      // 
      onPreview(file){
        console.log(this.addForm.fileList)
        this.url = file.name
        this.fun()
      },
      // 
      getRequestImageDelete({name}){
        return new Promise(async (resolve, reject) =>{
          const result = await requestImageDelete({name})
          console.log(result)
          if(result.code === 0){
            resolve(true)
          }else{
            resolve(false)
          }
        })
      },
      // 
      beforeUpload(file){
        let fileName = file.name.substring(file.name.lastIndexOf('.')+1)
        let testArr = ['jpeg','png','jpg','gif']
        if(!testArr.includes(fileName)){
          this.$message.error('\'jpeg\',\'png\',\'jpg\',\'gif\'')
            return false
        }
      },
      // 
      onSuccessHandle(response, file, fileList){ // 
        const {code,data} = response
        console.log(response)
        if(code === 0){
          file.name = data.name
          file.url = data.url
          console.log(file)
          this.$message.success('')
        }else {
          if(code === 1){ this.$message.error('')}
          else if(code === 2){this.$message.error('')}
          else{this.$message.error('')}
          fileList.splice(fileList.length-1,1)
        }
        this.addForm.fileList = fileList
      },
      // 
      async beforeRemove(file,fileList){
        return new Promise(async (resolve, reject) =>{
          const result = await this.$confirm(`${file.name}, ?`, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).catch(e =>{})
          if(result){ // 
            console.log(file.name)
            const result = await this.getRequestImageDelete({name:file.name})
            console.log(result)
            if(result){
              this.$message.success(`${file.name}`)
              resolve(true)
            }else{
              this.$message.error(`${file.name}`)
              reject(false)
            }
          }else{
            this.$message.info('')
            reject(false)
          }
        })
      },
      // 
      handleRemove(file,fileList){
        this.addForm.fileList = fileList
        console.log('// ',file,fileList,this.addForm.fileList);
      },
    },
    components: { // 
      BigImage
    }
  }
  /*
    beforeCreate () { // 

    },
    created () { // 

    },
    beforeMount () { // 

    },
    mounted() { //

    },
    beforeUpdate() { // 

    },
    updated() { // 

    },
    beforeDestroy() { // 

    },
    destroyed() { // 

    },
    computed: { // 

    },
    watch: { // 

    },
    */
</script>
