<template>
  <div class="Form">
    <el-form-item prop="goodName" label="">
      <el-input v-model="addForm.goodName"></el-input>
    </el-form-item>
    <el-form-item prop="goodPrice" label="">
      <el-input type="number" v-model="addForm.goodPrice"></el-input>
    </el-form-item>
    <el-form-item prop="goodWeight" label="">
      <el-input type="number" v-model="addForm.goodWeight"></el-input>
    </el-form-item>
    <el-form-item prop="goodNum" label="">
      <el-input type="number" v-model="addForm.goodNum"></el-input>
    </el-form-item>
    <el-form-item prop="goodSelect" label="" >
      <el-cascader
        :key="rendered"
        :options="goodsSelectOptions"
        @change = 'goodsSelectOptionsChange'
        v-model="addForm.goodSelect"
        :props="props">
      </el-cascader>
    </el-form-item>
    <el-pagination
      class="pagination"
      background
      @current-change="handleCurrentChange"
      :current-page="GoodCurrentPage"
      layout="total,prev,next,jumper"
      :total="categoryTotal" />
  </div>
</template>
<script>
  import {mapState,mapActions} from 'vuex'
  export default {
    props:{
      changedFun:{type:Function}
    },
    data() { // 
      let _self = this
      return {
        rendered:true,
        categoryTotal:0,
        GoodCurrentPage:0,
        props:{
          label:'categoryName',
          value: '_id',
          lazy: true,
          async lazyLoad (node, resolve) {
            if(node){
              const { level } = node;
              if(level!==0){
                const fatherCategoryId = node.value
                const result =await _self.getGoodCategory({level:level,fatherCategoryId})
                resolve(result)
              }
            }
          }
        }
      }
    },
    created () { // 
      this.getGoodCategory({level:0,pagenum:1,pagesize:20,callFun:(categoryTotal) =>{
        this.categoryTotal = categoryTotal
      }})
    },
    computed: { // 
      ...mapState(['addForm','goodsSelectOptions'])
    },
    /*
    beforeCreate () { // 
      
    },
    created () { // 
      
    },
    beforeMount () { // 
      
    },
    mounted() { //
      
    },
    beforeUpdate() { // 
      
    },
    updated() { // 
      
    },
    beforeDestroy() { // 
      
    },
    destroyed() { // 
      
    },
    computed: { // 
      
    },
    watch: { // 
      
    },
    */
    methods: { // 
      ...mapActions(['getGoodCategory']),
      goodsSelectOptionsChange(val){
        if(val.length<3){
          this.$message.error('')
          this.addForm.goodSelect = []
        }else{
          this.changedFun()
          console.log(this.addForm.goodSelect)
        }
      },
      handleCurrentChange(val){
        this.addForm.goodSelect = []
        this.GoodCurrentPage = val
        this.rendered = !this.rendered
        this.getGoodCategory({level:0,pagenum:val,pagesize:20})
      }
    },
    components: { // 

    }
  }
</script>
<style scoped lang='less'>
.Form{
  position: relative;
  .pagination{
    position: absolute;
    bottom: 2px;
    left: 300px;
  }
}
</style>