<template>
  <div>
    <el-table border stripe height="560" :data="GoodsList" style="width: 100%">
      <el-table-column type="index" label="#" />
      <el-table-column label="" prop="goodsName"/>
      <el-table-column label="()" prop="goodsPrice" width="200px"/>
      <el-table-column label="" prop="goodsWeight" width="200px"/>
      <el-table-column label="" width="340px">
        <template slot-scope="scope">
          <span>{{nowTime(scope.row.addTime)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="340px">
        <template slot-scope="scope">
          <el-button @click="updateGood(scope.$index,scope.row)" type="primary" size="mini" style="margin-left: 30px"></el-button>
          <el-button @click="deleteGood(scope.row)" size="mini" type="danger" style="margin-left: 30px"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  import {mapActions,mapGetters,mapState} from 'vuex'
  import {requestDeleteGood} from '../../../../../ajax/index'
  import {exchangeFun} from './commen'
  import {timeFun} from '../../../../../tools/timeFun'
  export default {
    data() { // 
      return {
        pagesize:20,
        selectStr:''
      }
    },
    created () { // 
      this.getRequestGoodsList({pagenum:1,pagesize:20})
      exchangeFun.$on('goodsSearchValue',(val) =>{
        this.selectStr = val
      })
    },
    computed: { // 
      ...mapGetters(['GoodsList','GoodsPageNum']),
      nowTime(){
        return (t) => timeFun(t)
      }
    },
    methods: { // 
      ...mapActions(['getRequestGoodsList','beSureUpdateGood']),
      getRequestDeleteGood({_id}){
        return new Promise(async(resolve, reject) =>{
          const result = await requestDeleteGood({_id})
          console.log(result)
          if(result.code === 0){
            resolve(true)
          }else{
            reject(false)
          }
        })
      },
      updateGood(index,value){
        console.log(index,value)
        value.goodsState = value.goodsState.toString()
        this.beSureUpdateGood({goodObj:value})
        this.$router.push('/productslist/update')
      },
      async deleteGood(item){
        console.log(item)
        const result =await this.$confirm(`(${item.goodsName}), ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(e =>this.$message({type: 'info',message: ''}))
        if(result === 'confirm'){
          const _id = item._id
          this.getRequestDeleteGood({_id})
            .then(
              () =>{
                this.getRequestGoodsList(
                  {pagenum:this.GoodsPageNum,pagesize:this.pagesize,selectStr:this.selectStr},
                  () =>{this.$message.success('')},
                  () =>{this.$message.error('')}
                )},
              () =>this.$message.error('')
            )
        }
      }
    },
    components: { // 

    }
  }
</script>
<style scoped lang=''>

</style>